
.vue-grid-layout {
  background: transparent;
}
.vue-grid-layout .smartwidget {
  height: inherit;
  width: inherit;
}

.smartwidget {
  background: #fff;
  -webkit-box-shadow: 0 0 10px 0 #e9e9e9;
          box-shadow: 0 0 10px 0 #e9e9e9;
  border: 1px solid #ebeef5;
  width: 100%;
}
.smartwidget .widget-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 48px;
  border-bottom: 1px solid #ebeef5;
}
.smartwidget .widget-header h2,
.smartwidget .widget-header h4 {
  display: inline-block;
  position: relative;
  width: auto;
  margin: 0;
  font-weight: normal;
  letter-spacing: 0;
}
.smartwidget .widget-header h2 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 16px;
}
.smartwidget .widget-header h2 .widget-header__title {
  padding: 0 10px;
}
.smartwidget .widget-header h4 {
  font-size: 12px;
  color: #777;
}
.smartwidget .widget-header .widget-header__prefix {
  background: #0076db;
  width: 2px;
  height: 16px;
  margin-left: 10px;
}
.smartwidget .widget-header .widget-header__toolbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding: 0;
  margin: 0;
}
.smartwidget .widget-header .widget-header__toolbar a {
  display: inline-block;
  text-decoration: none;
  text-align: center;
  height: 24px;
  line-height: 24px;
  padding: 0;
  margin: 0;
  color: #333;
  min-width: 35px;
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
  border-left: 1px solid rgba(0, 0, 0, 0.09);
}
.smartwidget .widget-body {
  position: relative;
  overflow: hidden;
  -webkit-transition: height .3s;
  transition: height .3s;
}
.smartwidget .widget-body .widget-body__content.fixed-height {
  overflow-y: scroll;
}
.smartwidget .widget-body .widget-body__footer {
  position: relative;
}
.smartwidget .widget-body .widget-body__footer.has-group {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}
.smartwidget.smartwidget-fullscreen {
  width: 100% !important;
  height: 100% !important;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
}
.smartwidget.smartwidget-fullscreen .widget-header {
  cursor: default;
}

.smart-widget__loading-mask {
  position: absolute;
  z-index: 2000;
  background-color: rgba(255, 255, 255, 0.9);
  margin: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.smart-widget__loading-mask .loading-spinner {
  top: 50%;
  margin-top: -21px;
  width: 100%;
  text-align: center;
  position: absolute;
}
.smart-widget__loading-mask .circular {
  width: 42px;
  height: 42px;
  -webkit-animation: loading-rotate 2s linear infinite;
          animation: loading-rotate 2s linear infinite;
}
.smart-widget__loading-mask .path {
  -webkit-animation: loading-dash 1.5s ease-in-out infinite;
          animation: loading-dash 1.5s ease-in-out infinite;
  stroke-dasharray: 90,150;
  stroke-dashoffset: 0;
  stroke-width: 2;
  stroke: #5282e4;
  stroke-linecap: round;
}
@-webkit-keyframes loading-rotate {
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading-rotate {
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes loading-dash {
0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -40px;
}
100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -120px;
}
}
@keyframes loading-dash {
0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
}
50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -40px;
}
100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -120px;
}
}
